import React from 'react';
import SkyLight from 'react-skylight';
import Page from '../elements/Page';
import noty from '../../common/noty/noty';
import store from '../../../store';
import { changeTemplate } from '../../../actions/h5Actions';
import Fetch from '../../../common/FetchIt';
import API_URL from '../../../common/url';
import t from '../../i18n';
import './saveTemplateDialog.less';

export default class SaveTemplateDialog extends React.Component {
    state = {
        name: '',
    };
    componentDidMount() {
        this.loadData(this.props.templateId);
    }
    loadData = templateId => {
        if (templateId) {
            Fetch.get(`${API_URL.template.show}${templateId}`).then(template => {
                this.setState({
                    name: template.name,
                });
            });
        }
    };
    componentWillReceiveProps(nextProps) {
        this.loadData(nextProps.templateId);
    }
    show = () => {
        this.previewModal.show();
    };
    hide = () => {
        this.previewModal.hide();
    };
    saveTemplate = () => {
        if (!this.state.name) {
            noty.error('');
            return;
        }
        Fetch.postJSON(API_URL.template.save, { id: this.props.templateId, name: this.state.name, pages: JSON.stringify(this.props.pages), published: false }).then(data => {
            noty.success(t('save_success'));
            store.dispatch(changeTemplate(data));
            this.hide();
        });
    };
    changeName = e => {
        this.setState({
            name: e.target.value,
        });
    };
    render() {
        const previewDialog = {
            height: 'auto',
            minHeight: '480px',
            width: '720px',
            margin: '0 auto',
            boxShadow: '0 4px 10px 0 ',
            borderRadius: '6px',
            left: 0,
            right: 0,
            top: '100px',
        };
        return (
            <SkyLight
                dialogStyles={previewDialog}
                hideOnOverlayClicked
                ref={com => { this.previewModal = com; }}
                title=""
            >
                <div className="saveTemplateDialog">
                    <div className="flex_row_between">
                        <div>
                            <div className="flex_row_start pages">
                                {
                                    this.props.pages.slice(0, 5).map(page => <div key={page.id}><div className="preview"><Page page={page} viewing /></div></div>)
                                }
                                {
                                    this.props.pages.length > 5 && <div><img src={require('./images/morepage.png')} /></div>
                                }
                            </div>
                            <div className="pageNum"> {this.props.pages.length} </div>
                        </div>
                        <div className="content">
                            <h4></h4>
                            <div><input placeholder="" onChange={this.changeName} value={this.state.name} /></div>
                            <button onClick={this.saveTemplate}></button>
                        </div>
                    </div>
                </div>
            </SkyLight>
        );
    }
}



