import BaseModal from './BaseModal';

export default class ImageModal extends BaseModal {
    constructor(src = '') {
        super();
        this.src = src;
        this.name = 'ImageModal';
        this.style = {
            width: '100px',
            height: '100px',
        };
        this.animations = [];
        this.action = {};
    }
}



