import BaseModal from './BaseModal';

export default class ShapeModal extends BaseModal {
    constructor(name = '') {
        super();
        this.shapeName = name;
        this.name = 'ShapeModal';
        this.style = {
            width: '100px',
            height: '100px',
            fill: '#00BCD3',
            stroke: 'none',
            strokeWidth: 0,
            fillOpacity: 1,
            transform: '0deg',
        };
        this.animations = [];
        this.action = {};
    }
}



