import React from 'react';
import FileUpload from 'react-fileupload';
import common from '../../../common/common';
import noty from '../noty/noty';

export default class FileUploader extends React.Component {
    render() {
        const propOptions = this.props.options;
        const options = {
            baseUrl: propOptions.baseUrl,
            dataType: 'json',
            chooseAndUpload: propOptions.chooseAndUpload === undefined ? true : propOptions.chooseAndUpload,
            accept: propOptions.accept ? propOptions.accept : 'image/*',
            multiple: propOptions.multiple === undefined ? false : propOptions.multiple,
            fileFieldName: 'file',
            paramAddToField: { access_token: common.getAccessToken(), isPublic: false },
            requestHeaders: {},
            chooseFile: function (files) {
                // noty.success('!');
                const imageArray = ['src/components/h5/header/images/audio.png',
                    'src/components/h5/header/images/fonts.png',
                    'src/components/h5/header/images/photo.png'];
                propOptions.uploadSuccess(imageArray[parseInt(Math.random() * 3)]);
            },
            beforeUpload: function (files, mill) {

            },
            doUpload: function (files, mill) {

            },
            uploading: function (progress) {

            },
            uploadSuccess: function (resp) {
                // propOptions.uploadSuccess(resp);
            },
            uploadError: function (err) {

            },
            uploadFail: function (resp) {

            },
        };
        return (
            <FileUpload options={options}>
                {this.props.children}
            </FileUpload>
        );
    }
}



