import React from 'react';
import { connect } from 'react-redux';
import { ActionCreators } from 'redux-undo';
import Panel from './panel/Panel';
import Header from './header/Header';
import store from '../../store';
import PageContainer from './pageContainer/PageContainer';
import { addPage, changeCourse } from '../../actions/h5Actions';
import PageModal from './modal/PageModal';
import Sidebar from './sidebar/Sidebar';
import Fetch from '../../common/FetchIt';
import API_URL from '../../common/url';
import noty from '../common/noty/noty';
import PreviewDialog from './dialog/PreviewDialog';
import SaveTemplateDialog from './dialog/SaveTemplateDialog';
import Auth from '../../common/Auth';
import t from '../i18n';
import './builder.less';

class Builder extends React.Component {
    constructor() {
        super();
        // new Auth().check();
    }

    componentDidMount = () => {
        this.addPage();
        store.dispatch(ActionCreators.clearHistory());
    };

    addPage = () => {
        store.dispatch(addPage(new PageModal().plainObject()));
    };

    delPage = () => {

    };

    publish = () => {
        Fetch.postJSON(API_URL.course.save, { id: this.props.courseId, name: `${new Date().getTime()}`, templateId: this.props.templateId, pages: JSON.stringify(this.props.pages), published: false }).then(data => {
            noty.success(t('publish_success'));
            store.dispatch(changeCourse(data));
        });
    };

    render() {
        return (
            <div>
                <Header
                    onPublish={this.publish}
                    onPreview={() => this.pageModal.show()}
                    onSaveTemplate={() => this.saveTemplateDialog.show()}
                />
                <div className="builder">
                    <Sidebar pages={this.props.pages} currentPage={this.props.currentPage} />
                    <PageContainer pages={this.props.pages} focusId={this.props.focus.id} currentPage={this.props.currentPage} />
                    <Panel focus={this.props.focus} currentPage={this.props.pages[this.props.currentPage]} />
                    <PreviewDialog ref={com => { this.pageModal = com; }} pages={this.props.pages} />
                    <SaveTemplateDialog ref={com => { this.saveTemplateDialog = com; }} pages={this.props.pages} />
                </div>
            </div>
        );
    }
}

const mapStateToProps = function (store) {
    return {
        courseId: store.h5State.present.courseId,
        templateId: store.h5State.present.templateId,
        pages: store.h5State.present.pages,
        currentPage: store.h5State.present.currentPage,
        focus: store.h5State.present.focus,
    };
};

export default connect(mapStateToProps)(Builder);



