import React from 'react';
import InputNumber from 'rc-input-number';
import 'rc-input-number/assets/index.css';
import 'rc-slider/assets/index.css';
import store from '../../../store';
import AnimationModal from '../modal/AnimationModal';
import { changeAnimation, addAnimation, delAnimation } from '../../../actions/h5Actions';
import './animationPanel.less';

export default class AnimationPanel extends React.Component {
    addAnimation = () => {
        store.dispatch(addAnimation(new AnimationModal().plainObject()));
    };
    render() {
        return (
            <div className="animationPanel">
                <div className="animationTitle" onClick={this.addAnimation}></div>
                {
                    this.props.focus.animations.map((value, index) => <AnimationSetting key={value.id} index={index} value={value} />)
                }
            </div>
        );
    }
}

class AnimationSetting extends React.Component {
    state = {
        shrinked: false,
    };
    changeDelay = delay => {
        store.dispatch(changeAnimation(this.props.index, Object.assign({}, this.props.value, {
            animationDelay: `${delay}s`,
        })));
    };

    changeDuration = duration => {
        store.dispatch(changeAnimation(this.props.index, Object.assign({}, this.props.value, {
            animationDuration: `${duration}s`,
        })));
    };

    changeIteration = iteration => {
        store.dispatch(changeAnimation(this.props.index, Object.assign({}, this.props.value, {
            animationIterationCount: iteration,
        })));
    };

    onChecked = e => {
        if (e.target.checked) {
            this.changeIteration('infinite');
        } else {
            this.changeIteration(1);
        }
    };

    onChange = e => {
        store.dispatch(changeAnimation(this.props.index, Object.assign({}, this.props.value, {
            className: e.target.value,
        })));
    };

    deleteAnimation = () => {
        store.dispatch(delAnimation(this.props.index));
    };

    shrink = () => {
        this.setState({
            shrinked: !this.state.shrinked,
        });
    };

    render() {
        const { value } = this.props;
        if (this.state.shrinked) {
            return (
                <div className="model_animation">
                    <h4><img src={require('./images/pulldown.png')} onClick={this.shrink} className="rotate90" />  <div className="floatRight" onClick={this.deleteAnimation}>X</div></h4>
                </div>
            );
        }
        return (
            <div className="model_animation">
                <h4><img src={require('./images/pulldown.png')} onClick={this.shrink} />  <div className="floatRight" onClick={this.deleteAnimation}>X</div></h4>
                <div className="detail"><span className="label"></span>
                    <select onChange={this.onChange} defaultValue={value.className}>
                        <option value=""></option>
                        <option value="animated fadeInDown"></option>
                        <option value="animated fadeInUp"></option>
                    </select>
                </div>
                <div><span className="label"></span><InputNumber value={parseFloat(value.animationDelay).toFixed(1)} min={0} step={0.1} onChange={this.changeDelay} /></div>
                <div><span className="label"></span><InputNumber value={parseFloat(value.animationDuration).toFixed(1)} min={0} step={0.1} onChange={this.changeDuration} /></div>
                <div>
                    <span className="label"></span>
                    <InputNumber defaultValue={value.animationIterationCount === 'infinite' ? 1 : value.animationIterationCount} min={1} onChange={this.changeIteration} />
                    <div className="floatRight"><input type="checkbox" onChange={this.onChecked} defaultValue={value.animationIterationCount === 'infinite'} /></div>
                </div>
            </div>
        );
    }
}



