import React from 'react';

const RadioGroup = ({ data = [], value = '', onChange }) => {
    const handleChange = val => {
        onChange(val);
    };
    return (
        <div>
            {
                data.length > 0 ? data.map((item, index) => <RadioItem
                    key={`radio${index}`}
                    name={item.name} itemVal={item.value}
                    value={value} onChange={handleChange}
                />) : null
            }
        </div>
    );
};

const RadioItem = ({ name, value, itemVal, onChange }) => {
    const handleClick = () => {
        onChange(itemVal);
    };
    const handleChange = () => {
        // nothing
    };
    return (
        <div className="flex_row_start flex_vertical_middle cursor-pointer" onClick={handleClick}>
            <input type="radio" value={itemVal} checked={itemVal === value} onChange={handleChange}/>
            <span>{name}</span>
        </div>
    );
};
export default RadioGroup;



