import BaseModal from './BaseModal';

export default class WordModal extends BaseModal {
    constructor(text = '') {
        super();
        this.text = text;
        this.style = {
            height: '26px',
            lineHeight: '26px',
            fontSize: '14px',
            width: '92px',
            textAlign: 'center',
        };
        this.name = 'WordModal';
        this.pinyin = false;
        this.symbol = [];
        this.animations = [];
        this.contenteditable = false;
        this.action = {};
    }
}



