import React from 'react';
import Pagination from 'rc-pagination';
import Fetch from '../../../common/FetchIt';
import API_URL from '../../../common/url';
import Page from '../elements/Page';

function TemplatePage({ index, template }) {
    const pages = JSON.parse(template.pages);
    return (
	//template.pages = template.pages.replace(/(\")|(\')/g,'\\"');
        <div className={`templatePage ${index % 3 === 0 ? 'rowStart' : ''}`}>
            <div className="preview">
                <Page page={pages[0]} viewing template />
            </div>
        </div>
    );
}

class TemplateList extends React.Component {
    state = {
        templates: [],
        total: 1,
    };
    componentDidMount() {
        this.loadData();
    }
    loadData = (page = 1) => {
        Fetch.get(`${API_URL.template.list}?page=${page}&isPublic=${this.props.isPublic}`).then(data => {
            this.setState({
                templates: data.content,
                total: data.totalElements,
            });
        });
    };
    changePage = page => {
        this.loadData(page);
    };
    render() {
        return (
            <div>
                <div className="flex_row_start">
                    {
                        this.state.templates.map((template, index) => <TemplatePage key={template.id} index={index} template={template} />)
                    }
                </div>
                <Pagination onChange={this.changePage} total={this.state.total} pageSize={9} simple />
            </div>
        );
    }
}
export default TemplateList;



