import React from 'react';
import SkyLight from 'react-skylight';
import AudioModal from '../modal/AudioModal';
import FileUploader from '../../common/fileUpload/fileUploader';
import store from '../../../store';
import { addElements } from '../../../actions/h5Actions';
import Fetch from '../../../common/FetchIt';
import API_URL from '../../../common/url';
import Category from './Category';
import './addAudioDialog.less';
import Noty from '../../common/noty/noty';

const url = 'http://editor.gopopon.com/';
export default class AddAudioDialog extends React.Component {
    state = {
        contentLi: '',
        audios: [],
        categoryId: 0,
    };

    show() {
        this.audioModal.show();
    }
    chooseMangAudio = () => {
        const elements = this.state.audios.map(audio => new AudioModal(audio.url).plainObject());
        store.dispatch(addElements(elements));
        this.saveRecent();
        this.audioModal.hide();
    };
    saveRecent = () => {
        let audios = localStorage.getItem('audios');
        audios = audios ? JSON.parse(audios) : [];
        audios = audios.concat(this.state.audios);
        localStorage.setItem('audios', JSON.stringify(audios));
    };
    select = (name, e) => {
        const value = e.target ? e.target.value : e;
        this.setState({
            [name]: value,
        });
    };
    selectedAudio = item => {
        const index = this.state.audios.findIndex(audio => audio.id === item.id);
        if (index === -1) {
            this.setState({
                audios: this.state.audios.concat([item]),
            });
        }
    };
    previewAudio = () => {
        this.audioModal.hide();
        this.previewModal.show();
    };
    deleteAudio = i => {
        const audioList = this.state.audios.filter((item, index) => index !== i);
        this.setState({
            audios: audioList,
        });
    };
    render() {
        const addAudioDialog = {// 
            width: '780px',
            height: 'auto',
            color: '#505766',
            zIndex: '101!important',
            background: '#f9f9f9',
            margin: '0 auto',
            boxShadow: '0 4px 10px 0',
            left: 0,
            right: 0,
            top: '100px',
            borderRadius: '6px',
        };

        return (
            <div>
                <SkyLight
                    dialogStyles={addAudioDialog}
                    hideOnOverlayClicked
                    ref={com => { this.audioModal = com; }}
                    title=""
                >
                    <div className="addImageBody audioDialog flex_row_start">
                        <div className="left flex_column_between">
                            <ul className="cursor-pointer">
                                <li
                                    onClick={() => this.select('contentLi', '')}
                                    className={`${this.state.contentLi === '' ? 'selectLi' : ''}`}
                                >
                                    
                                </li>
                                <li
                                    onClick={() => this.select('contentLi', '')}
                                    className={`${this.state.contentLi === '' ? 'selectLi' : ''}`}
                                >
                                    
                                </li>
                                <li
                                    onClick={() => this.select('contentLi', '')}
                                    className={`${this.state.contentLi === '' ? 'selectLi' : ''}`}
                                >
                                    
                                </li>
                                <li
                                    onClick={() => this.select('contentLi', '')}
                                    className={`${this.state.contentLi === '' ? 'selectLi' : ''}`}
                                >
                                    
                                </li>
                            </ul>
                            <button className="uploadBtn" onClick={this.previewAudio}></button>
                        </div>
                        <div className="right">
                            <Audios
                                onChoose={item => this.selectedAudio(item)}
                                current={this.state.contentLi}
                                changeSelect={this.select}
                                categoryId={this.state.categoryId}
                            />
                            <ul className="page">
                                {
                                    this.state.audios.length ? <li >:</li> : null
                                }

                                {
                                    this.state.audios.map((item, index) =>
                                        <li key={item.id}>
                                            <p>{item.musicName}</p>
                                            <div className="deleteAudio" onClick={() => this.deleteAudio(index)}></div>
                                        </li>,
                                    )
                                }
                                {
                                    this.state.contentLi !== '' && <div className="chooseAudioBtn" onClick={this.chooseMangAudio}></div>
                                }
                            </ul>
                        </div>
                    </div>
                </SkyLight>
                <PreviewDialog ref={com => { this.previewModal = com; }} />
            </div>
        );
    }
}

class Audios extends React.Component {
    state = {
        audios: [],
        play: null,
        hover: null,
        categoryId: 0,
    };
    componentDidMount() {
        this.loadData();
    }
    loadData = (isPublic = true) => {
        Fetch.get(`${API_URL.audio.list}?isPublic=${isPublic}&categoryId=${this.state.categoryId}&keyword=${this.keyword}`).then(data => {
            this.setState({
                audios: data,
            });
        });
    };
    componentWillReceiveProps(nextProps) {
        switch (nextProps.current) {
        case '': this.loadData(true); break;
        case '': this.loadData(false); break;
        case '': {
            const audios = localStorage.getItem('audios') ? JSON.parse(localStorage.getItem('audios')) : [];
            this.setState({ audios });
            break;
        }
        default: break;
        }
        this.setState({
            current: nextProps.current,
        });
    }
    previewPlay = (item, index, e) => {
        e.stopPropagation();
        const audio = document.getElementById(`audio${item.id}`);
        if (audio.paused) {
            audio.play();
            this.handlePlayState(index);
        } else {
            audio.pause();
            this.handlePlayState(null);
        }
    };
    handlePlayState = idx => {
        this.setState({
            play: idx,
        });
    };
    handleOver = index => {
        this.setState({
            hover: index,
        });
    };
    changeKeyword = e => {
        this.keyword = e.target.value;
    };

    search = () => {
        this.loadData(true);
    };
    render() {
        const { onChoose } = this.props;
        if (this.state.current === '') {
            return (
                <div className="flex_row_center audio_list">
                    <FileUploader
                        options={{
                            baseUrl: API_URL.audio.upload,
                            multiple: true,
                            accept: 'audio/*',
                            uploadSuccess: () => {
                                Noty.success('');
                                this.props.changeSelect('contentLi', '');
                            },
                        }}
                    >
                        <button ref="chooseAndUpload" className="uploadBtn"></button>
                    </FileUploader>
                </div>
            );
        }
        const butSrc = {
            play: 'src/components/h5/dialog/images/play',
            pause: 'src/components/h5/dialog/images/pause',
        };
        return (
            <div>
                {
                    this.state.current === '' ? <div className="top flex_row_start flex_vertical_top">
                        <Category type="audio" onClick={category => this.setState({ categoryId: category.id })} />
                        <div className="searchItem flex_row_between">
                            <input onChange={this.changeKeyword} />
                            <button className="flex_row_center flex_vertical_middle" onClick={this.search}>
                                <img src={require('./images/search.png')} />
                            </button>
                        </div>
                    </div> : null
                }
                <ul className="audio_list">
                    {
                        this.state.audios.map((item, index) => <li
                                onMouseEnter={() => this.handleOver(index)}
                                onMouseLeave={() => this.handleOver(null)}
                                onClick={() => onChoose(item)}
                                key={item.id}
                                className="flex_row_start flex_vertical_middle"
                            >
                                <button
                                    style={{ backgroundImage: `url(${this.state.hover === index ? `${this.state.play === index ? butSrc.play : butSrc.pause}_selected.png` : `${this.state.play === index ? butSrc.play : butSrc.pause}_normal.png`})` }}
                                    className="controlBtn"
                                    onClick={args => this.previewPlay(item, index, args)}
                                />
                                <audio id={`audio${item.id}`} src={API_URL.domain + item.musicPath} onEnded={() => this.handlePlayState(null)} />
                                {item.musicName}
                            </li>,
                        )
                    }
                </ul>
            </div>
        );
    }
}

class PreviewDialog extends React.Component {
    state = {
        audio: '',
    };
    show() {
        this.previewModal.show();
    }
    hide = () => {
        this.previewModal.hide();
    };
    addAudio = () => {
        if (!this.state.audio || this.state.audio.indexOf('http') !== 0) {
            Noty.error('http');
            return;
        }
        store.dispatch(addElements(new AudioModal(this.state.audio).plainObject()));
        this.previewModal.hide();
    };
    saveValue = e => {
        this.setState({
            audio: e.target.value,
        });
    };
    render() {
        const previewDialog = {// 
            height: 'auto',
            minHeight: '320px',
            width: '780px',
            margin: '0 auto',
            left: 0,
            right: 0,
            top: '200px',
            background: '#F9F9F9',
            boxShadow: '0 4px 10px 0 rgba(0,0,0,0.20)',
            borderRadius: '6px',
        };
        return (
            <SkyLight
                dialogStyles={previewDialog}
                hideOnOverlayClicked
                ref={com => { this.previewModal = com; }}
                title=""
            >
                <p className="audioDesc"></p>
                <input type="text" className="audioInput" placeholder="" onChange={this.saveValue} />
                <ul className="audioBtn">
                    <li onClick={this.addAudio}></li>
                    <li onClick={this.hide}></li>

                </ul>
            </SkyLight>
        );
    }
}



