import React from 'react';
import SkyLight from 'react-skylight';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
import store from '../../../store';
import API_URL from '../../../common/url';
import { changeImage, changePageStyle } from '../../../actions/h5Actions';
import RadioGroup from '../common/radioGroup/radioGroup';

class Ratio {
    constructor(str) {
        this.ratioArray = str.split('/');
    }
    numerator() {
        return Number.parseInt(this.ratioArray[0]);
    }
    denominator() {
        return Number.parseInt(this.ratioArray[1]);
    }
}
export default class CropperDialog extends React.Component {
    state = {
        ratio: '1/1',
        resultSrc: '',
    };
    show = () => {
        this.cropperModal.show();
    };
    crop = () => {
        const imageSrc = this.cropper.getCroppedCanvas().toDataURL();
        this.handleState('resultSrc', imageSrc);
    };
    handleState = (name, value) => {
        this.setState({
            [name]: value,
        });
    };
    handleOk = () => {
        if (this.props.type === 'image') {
            store.dispatch(changeImage(this.state.resultSrc));
        }
        if (this.props.type === 'backImage') {
            store.dispatch(changePageStyle({
                backgroundImage: `url(${this.state.resultSrc})`,
                backgroundRepeat: 'no-repeat',
                backgroundSize: '100%',
            }));
        }
        this.handleCancel();
    };
    handleCancel = () => {
        this.cropperModal.hide();
    };
    render() {
        let image = this.props.focus.src;
        if (this.props.type === 'backImage') {
            image = this.props.focus.style.backgroundImage ? this.props.focus.style.backgroundImage.substring(4, this.props.focus.style.backgroundImage.length - 1) : '';
        }
        image = API_URL.domain + image;
        const ratio = new Ratio(this.state.ratio);
        return (
            <SkyLight
                hideOnOverlayClicked
                ref={com => { this.cropperModal = com; }}
                title=""
            >
                <div className="flex_row_start">
                    {
                        image ? <Cropper
                            ref={com => this.cropper = com}
                            src={image}
                            style={{ height: 300, width: '70%' }}
                            aspectRatio={ratio.numerator() / ratio.denominator()}
                            guides={false}
                            crop={this.crop}
                        /> : null
                    }
                    <RadioGroup
                        data={[{
                            name: '1:1',
                            value: '1/1',
                        }, {
                            name: '4:3',
                            value: '4/3',
                        }, {
                            name: '3:4',
                            value: '3/4',
                        }, {
                            name: '',
                            value: '375/667',
                        }]} value={this.state.ratio} onChange={args => this.handleState('ratio', args)}
                    />
                </div>
                <div className="flex_row_end">
                    <button onClick={this.handleOk}></button>
                    <button onClick={this.handleCancel}></button>
                </div>
            </SkyLight>
        );
    }
}



