import BaseModal from './BaseModal';

export default class InputModal extends BaseModal {
    constructor(type = 'text', placeholder = '') {
        super();
        this.name = 'InputModal';
        this.placeholder = placeholder;
        this.style = {};
        this.type = type;
    }
}



