import React from 'react';
import { ContextMenuTrigger } from 'react-contextmenu';
import H5ContextMenu from './H5ContextMenu';
import Page from '../elements/Page';
import './pageContainer.less';
import Sidebar from './Sidebar';

class PageContainer extends React.Component {
    render() {
        return (
            <div className="pageContainer">
                <div className="phone">
                    <ContextMenuTrigger id="contextMenu" holdToDisplay={-1}>
                        {
                            this.props.pages.map(
                                (page, index) => this.props.currentPage === index ? <Page key={page.id} page={page} focusId={this.props.focusId} /> : null,
                            )
                        }
                    </ContextMenuTrigger>
                    <Sidebar />
                </div>
                <H5ContextMenu />
            </div>
        );
    }
}

export default PageContainer;



