import React from 'react';
import SkyLight from 'react-skylight';
import VideoModal from '../modal/VideoModal';
import Noty from '../../common/noty/noty';
import store from '../../../store';
import { addElements } from '../../../actions/h5Actions';
import './addVideoDialog.less';

export default class AddVideoDialog extends React.Component {
    show() {
        this.videoModal.show();
    }
    addVideo = () => {
        if (!this.input.value || this.input.value.trim().indexOf('<iframe') !== 0) {
            Noty.error('');
            return;
        }
        store.dispatch(addElements(new VideoModal(this.input.value.trim()).plainObject()));
        this.videoModal.hide();
    };
    cancelClick = () => {
        this.videoModal.hide();
    };

    render() {
        return (
            <SkyLight hideOnOverlayClicked ref={com => { this.videoModal = com; }} title="">
                <div>
                    <p className="pTop"></p>
                    <input spellCheck="false" className="codeInpt" type="text" placeholder="" ref={input => this.input = input} />
                    <p className="Remarks">
                        <a href="javascript:void(0)"></a><a href="javascript:void(0)">Youtube</a>
                    </p>
                    <p className="positionP">
                        <button className="cancelVideo" onClick={this.cancelClick}></button>
                        <button className="determineVideo" onClick={this.addVideo}></button>
                    </p>
                </div>
            </SkyLight>
        );
    }
}



