import React from 'react';
import Tabs from 'react-simpletabs';
import 'react-simpletabs/dist/react-simpletabs.css';
import WordPanel from './WordPanel';
import ImagePanel from './imagePanel';
import AnimationPanel from './AnimationPanel';
import PageSetPanel from './pageSetPanel';
import ShapePanel from './shapePanel';
import RecentShapePanel from './RecentShapePanel';
import ActionPanel from './ActionPanel';
import TemplatePanel from './TemplatePanel';
import './panel.less';

export default function Panel({ focus, currentPage }) {
    const panels = [];
    switch (focus.name) {
    case 'WordModal': {
        panels.push(<Tabs.Panel title="" key="style"><WordPanel focus={focus} /></Tabs.Panel>);
        panels.push(<Tabs.Panel title="" key="animation"><AnimationPanel focus={focus} /></Tabs.Panel>);
        panels.push(<Tabs.Panel title="" key="action"><ActionPanel focus={focus} page={currentPage} /></Tabs.Panel>);
        break;
    }
    case 'ImageModal': {
        panels.push(<Tabs.Panel title="" key="style"><ImagePanel focus={focus} /></Tabs.Panel>);
        panels.push(<Tabs.Panel title="" key="animation"><AnimationPanel focus={focus} /></Tabs.Panel>);
        break;
    }
    case 'ShapeModal': {
        if (focus.id === 0) {
            panels.push(<Tabs.Panel title="" key="shape"><ShapePanel focus={focus} /></Tabs.Panel>);
            panels.push(<Tabs.Panel title="" key="animation"><RecentShapePanel /></Tabs.Panel>);
        } else {
            panels.push(<Tabs.Panel title="" key="style"><ShapePanel focus={focus} /></Tabs.Panel>);
            panels.push(<Tabs.Panel title="" key="animation"><AnimationPanel focus={focus} /></Tabs.Panel>);
            panels.push(<Tabs.Panel title="" key="action"><ActionPanel focus={focus} page={currentPage} /></Tabs.Panel>);
        }
        break;
    }
    case 'PageModal': {
        panels.push(<Tabs.Panel title="" key="pageSet"><PageSetPanel focus={focus} /></Tabs.Panel>);
        break;
    }
    default:
        panels.push(<Tabs.Panel title="" key="template"><TemplatePanel isPublic /></Tabs.Panel>);
        panels.push(<Tabs.Panel title="" key="myTemplate"><TemplatePanel isPublic={false} /></Tabs.Panel>);
        break;
    }
    if (panels.length === 0) {
        return null;
    }
    return (
        <div>
            <Tabs className="panel">
                {
                    panels.map(panel => panel)
                }
            </Tabs>

        </div>
    );
}



